import Drive from '@/tool/classFactory/car.js';
export default {
    name: "home",
    data () {
        return {

        }
    },
    created () {
        this.getMember()
    },
    methods:{
        getMember(){
            const that = this;
            Drive.prototype.getMember().then(res => {
                let list = res.data.data || [];
                that.userMsg = list
                console.log(list)
            });
        },
        hrefChoice(){
            this.$router.push({ path:'/choice' })
        }
    }
}



